@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/material-design-icons/MaterialIcons-Regular.eot);
  /* For IE6-8 */
  src: local('Material Icons'), local('MaterialIcons-Regular'), url(../fonts/material-design-icons/MaterialIcons-Regular.woff2) format('woff2'), url(../fonts/material-design-icons/MaterialIcons-Regular.woff) format('woff'), url(../fonts/material-design-icons/MaterialIcons-Regular.ttf) format('truetype');
}
/****
* http://google.github.io/material-design-icons/
*/
.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  /* Preferred icon size */
  display: inline-block;
  width: 1em;
  height: 1em;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;
  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
  /* Support for IE. */
  font-feature-settings: 'liga';
}
@font-face {
  font-family: 'open_sansregular';
  src: url('../fonts/open-sans/opensans-regular.eot');
  src: url('../fonts/open-sans/opensans-regular.eot?#iefix') format('embedded-opentype'), url('../fonts/open-sans/opensans-regular.woff2') format('woff2'), url('../fonts/open-sans/opensans-regular.woff') format('woff'), url('../fonts/open-sans/opensans-regular.ttf') format('truetype'), url('../fonts/open-sans/opensans-regular.svg#open_sansregular') format('svg');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'exoregular';
  src: url('../fonts/exo/exo-regular.eot');
  src: url('../fonts/exo/exo-regular.eot?#iefix') format('embedded-opentype'), url('../fonts/exo/exo-regular.woff2') format('woff2'), url('../fonts/exo/exo-regular.svg#exoregular') format('svg'), url('../fonts/exo/exo-regular.woff') format('woff'), url('../fonts/exo/exo-regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
/****
* Colour palette from http://www.colorhunt.co/hot
* http://i.imgur.com/w1pPtl0.png
*/
html {
  font-size: 20px;
  line-height: 1.6;
  /****
  overflow-y: scroll; to force a scrollbar so as to prevent
  the page jumping because the width changes
  */
  overflow-y: scroll;
}
body {
  color: #444;
  font-family: "open_sansregular", sans-serif;
  display: flex;
  flex-direction: column;
}
/****
  Utilities
*/
.warning {
  color: #F1C40F !important;
}
.hide {
  display: none !important;
}
.hide-not-i {
  display: none;
}
.showFlex {
  display: flex !important;
}
.showBlock {
  display: block !important;
}
.flexWrapperColumn {
  display: flex;
  flex-direction: column;
}
.absolute {
  position: absolute !important;
}
.navBar-materialIcon-selected {
  color: #2670A2 !important;
}
.lightBlue {
  color: #90C4E6 !important;
}
.navHeader {
  position: fixed;
  top: 0;
  z-index: 99;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}
.navHeader nav {
  background-color: #3498DB;
  display: flex;
  flex: 1 1 4rem;
}
.navHeader .brandLogo {
  /****
  * Element is both a flex item for it's parent container,
  * and a flex container itself for it's child element
  */
  flex: 1 1 auto;
  display: flex;
  align-items: center;
  font-family: 'exoregular';
}
.navHeader .brandLogo a {
  flex: 1 1 auto;
  color: #77BBEB;
  text-align: right;
  padding-right: 1rem;
  text-decoration: none;
}
.navHeader .brandLogo a h1 {
  font-size: 2.4rem;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: flex-end;
  text-rendering: geometricPrecision;
}
.navHeader .brandLogo a:hover {
  color: #2670A2;
}
.navHeader .searchContainer {
  /****
  * Element is both a flex item for it's parent container,
  * and a flex container itself for it's child element
  */
  flex: 2 2 auto;
  display: flex;
  border-top: 3px solid #3498DB;
  border-bottom: 3px solid #3498DB;
}
.navHeader #searchForm {
  flex: 1 1 auto;
  display: flex;
}
.navHeader #searchForm > input {
  flex: 1 1 auto;
  display: flex;
  font-size: 1.5rem;
  padding-left: 1rem;
}
.navHeader .rightContainer {
  flex: 2 1 auto;
  display: flex;
}
/***
* Material design icons in the header
*/
.navHeader .material-icons {
  font-size: 3rem;
  color: #77BBEB;
}
.navHeader .dateFilter {
  /****
  * Element is both a flex item for it's parent container,
  * and a flex container itself for it's child element
  */
  flex: 0 1 4rem;
  display: flex;
}
.navHeader .dateFilter a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  text-decoration: none;
}
.navHeader .dateFilter a:hover .material-icons {
  color: #2670A2;
}
.navHeader .looseSearch {
  flex: 0 1 4rem;
  display: flex;
  display: none;
}
.navHeader .looseSearch a {
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.navHeader .looseSearch a i {
  background-image: url("/images/goose.svg");
  background-repeat: no-repeat;
  background-position: center center;
}
.navHeader .looseSearch a:hover i {
  background-image: url("/images/goosehover.svg");
  background-repeat: no-repeat;
  background-position: center center;
  cursor: pointer;
}
.navHeader .addPage {
  flex: 0 1 4rem;
  display: flex;
}
.navHeader .addPage a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  text-decoration: none;
}
.navHeader .addPage a:hover .material-icons {
  color: #2670A2;
}
.navHeader .settings-etal {
  flex: 0 1 4rem;
  display: flex;
}
.navHeader .settings-etal a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  text-decoration: none;
}
.navHeader .settings-etal a:hover .material-icons {
  color: #2670A2;
}
/****
* Header Nav subBars
*/
.navHeader .subBar {
  background-color: #ECF0F1;
  display: flex;
  flex: 1 1 auto;
  justify-content: space-around;
  box-shadow: 0px 15px 67px 0px rgba(0, 0, 0, 0.64);
}
/****
* Date Filter
*/
.navHeader .dateFilterSettings {
  height: 50px;
  font-size: 1rem;
  color: #3498DB;
  align-items: center;
}
.navHeader .dateFilterSettings .fromToContainer {
  flex: 1 1 auto;
  /* align-items: stretch; */
  display: flex;
}
.navHeader .dateFilterSettings .fromContainer {
  display: flex;
  width: 15rem;
}
.navHeader .dateFilterSettings .toContainer {
  display: flex;
  width: 14rem;
}
.navHeader .dateFilterSettings .shortcutsContainer {
  display: flex;
  width: 9rem;
  border-left-width: 1px;
  border-left-style: solid;
  border-left-color: #3498DB;
}
.navHeader .dateFilterSettings .nice-select {
  border-radius: 0;
  border: none;
  float: none;
  font-size: inherit;
  height: inherit;
  line-height: inherit;
  background-color: #ECF0F1;
}
.navHeader .dateFilterSettings .nice-select .list {
  border-radius: 0;
  margin-top: 0;
  background-color: #ECF0F1;
}
/****
* Add Page Urls
*/
.navHeader .addPageUrls {
  flex: 1 1 auto;
  flex-direction: column;
}
.navHeader .addPageUrls textarea {
  display: flex;
  flex: 1 1 auto;
  font-size: 0.9rem;
  padding: 2rem;
  text-align: center;
  line-height: 1rem;
}
.navHeader .addPageUrls textarea::placeholder {
  text-align: center;
}
.navHeader .addPageUrls .progressInfo {
  display: flex;
  flex: 1 1 auto;
  text-align: center;
  padding: 2rem;
  justify-content: center;
}
.navHeader .addPageUrls .progressInfo ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.navHeader .addPageUrls .addUrlsProgress {
  display: flex;
  background-color: #ECF0F1;
  height: 50px;
  font-size: 24px;
  line-height: 50px;
}
.navHeader .addPageUrls .addUrlsProgress .progressBar {
  display: flex;
  background: #F1C40F;
  width: 0;
}
.navHeader .addPageUrls .addUrlsProgress .errorOKbutton {
  display: flex;
  background: #F1C40F;
  width: 0;
  justify-content: center;
  align-items: center;
}
.navHeader .addPageUrls .addUrlsProgress .errorOKbutton:hover {
  color: #2670A2;
  cursor: pointer;
}
.navHeader .addPageUrls .addPageButtons {
  display: flex;
  flex: 1 1 auto;
  color: white;
  height: 50px;
  font-size: 24px;
  line-height: 50px;
}
.navHeader .addPageUrls .addPageButtons div:hover {
  color: #2670A2;
  cursor: pointer;
}
.navHeader .addPageUrls .urlCancelButton {
  flex: 1 1 auto;
  background-color: #34495E;
  border: none;
  text-align: center;
}
.navHeader .addPageUrls .urlSaveButton {
  flex: 1 1 auto;
  border: none;
  text-align: center;
  background-color: #F1C40F;
}
.navHeader .settingsAndHelp {
  flex: 1 1 auto;
}
/***
Some Extra Form Element Resets
*/
textarea,
input,
input[type="text"],
input[type="email"],
input[type="password"],
input[type="search"],
input[type="color"],
input[type="date"],
input[type="datetime-local"],
input[type="month"],
input[type="number"],
input[type="tel"],
input[type="time"],
input[type="url"],
input[type="week"],
input[list] {
  outline: 0;
  border: none;
  box-shadow: none;
  border-radius: 0;
  -webkit-appearance: none;
}
input,
textarea,
select,
option,
optgroup,
button,
legend,
fieldset {
  outline: none;
}
