/****
* Index/Search page
****/
.searchPage {
  /*display: flex;*/
}
.searchPage #resultsOuterContainer {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 7rem;
  -webkit-transform: translateZ(0);
}
.searchPage #resultsContainer {
  flex: 0 1 44rem;
  display: flex;
  flex-direction: column;
  margin: 0 5rem;
}
.searchPage #resultsCount {
  display: flex;
  justify-content: center;
  position: relative;
  top: -1rem;
  opacity: 0.2;
}
.searchPage .result {
  flex: 1 1 auto;
  margin-bottom: 1.8rem;
  display: flex;
}
.searchPage .result a {
  text-decoration: none;
  color: #3498DB;
}
.searchPage .result a:hover {
  text-decoration: underline;
}
.searchPage .mainDetails {
  flex: 1 1 auto;
}
/****
 Make super long words/urls break and not overflow
*/
.searchPage .mainDetails * {
  word-wrap: break-word;
  overflow-wrap: break-word;
  word-break: break-word;
}
.searchPage .mainResultLink {
  display: flex;
  flex-direction: column;
}
.searchPage .mainResultLink.warning {
  flex-direction: row;
}
.searchPage .mainResultLink {
  font-size: 1.2rem;
  line-height: 1;
}
.searchPage .mainResultLink a:hover {
  color: #2670A2;
  text-decoration: none;
}
.searchPage .mainResultLink .material-icons {
  position: relative;
  top: -0.4rem;
}
.searchPage .mainResultLink .material-icons:hover {
  cursor: default;
}
.searchPage .resultUrlText {
  color: #92C8EF;
  font-size: 0.7rem;
}
.searchPage .description {
  font-size: 0.93rem;
}
.searchPage .description {
  margin: 0;
}
.searchPage .description strong {
  color: #2670A2;
}
.searchPage .safeBrowsing {
  color: #F1C40F;
  font-size: 0.8rem;
  margin: 0.1rem 0;
}
.searchPage .safeBrowsing > div {
  display: flex;
  flex-direction: column;
}
.searchPage .safeBrowsing > div:nth-child(n+2) {
  margin-top: 0.4rem;
}
.searchPage .safeBrowsing a {
  color: inherit;
}
.searchPage .safeBrowsing .material-icons {
  margin-right: 0.4rem;
  flex: 0 1 auto;
}
.searchPage .safeBrowsingBriefDescription {
  display: flex;
}
.searchPage .safeBrowsingBriefDescription > a {
  display: flex;
  align-items: center;
  flex: 1 1;
}
.searchPage .safeBrowsingBriefDescription > a:hover {
  text-decoration: none;
}
.searchPage .safeBrowsingExplination {
  display: none;
  margin-left: 1.6rem;
}
.searchPage .safeBrowsingExplination a {
  color: #2670A2;
}
.searchPage .googleAdvisory {
  font-size: 0.6rem;
}
.searchPage .metaIconsContainer {
  display: flex;
  flex: 0 0 auto;
}
.searchPage .metaIcons {
  color: #ECF0F1;
  display: flex;
}
.searchPage .metaIcons a {
  color: inherit;
}
.searchPage .metaIcons a:hover {
  text-decoration: none;
  color: #77BBEB;
}
/****
* Global
*/
/*****
54.5em width and wider
*****/
@media (min-width: 54.5em) {
  .navHeader .brandLogo .brandLogoSmall {
    display: none;
  }
  .navHeader .brandLogo .brandLogoLarge {
    display: flex;
  }
}
/*****
57.7em width and less
*****/
/*****
54.5em width and less
*****/
@media (max-width: 54.5em) {
  html {
    font-size: 12px;
  }
  .navHeader .brandLogo {
    padding-left: 0.6rem;
    flex-grow: 0;
  }
  .navHeader .brandLogo .brandLogoSmall {
    display: none;
  }
  .navHeader .rightContainer {
    flex-grow: 0;
  }
  .navHeader .rightContainer > div {
    padding: 0 0.5rem;
  }
  .navHeader .addPageUrls textarea {
    font-size: 1.2rem;
  }
  .navHeader .addPageUrls .addPageButtons {
    font-size: 1.8rem;
  }
  /****
    Search Page
  */
  .searchPage #resultsContainer {
    flex-basis: 58rem;
  }
  .searchPage .mainResultLink {
    font-size: 1.9rem;
  }
  .searchPage .mainResultLink .material-icons {
    top: -1.1rem;
  }
  .searchPage .resultUrlText {
    font-size: 1.1rem;
  }
  .searchPage .safeBrowsing {
    font-size: 1.3rem;
  }
  .searchPage .description {
    font-size: 1.3rem;
  }
  .searchPage .metaIcons a {
    font-size: 2.4rem;
  }
}
/*****
40em width and less
*****/
@media (max-width: 40em) {
  .navHeader .brandLogo .brandLogoSmall {
    display: flex;
  }
  .navHeader .brandLogo .brandLogoLarge {
    display: none;
  }
}
/*****
35.8em width and less
*****/
@media (max-width: 35.8em) {
  .searchPage #resultsContainer {
    margin: 0 3rem;
  }
}
/*****
32.1em width and less
*****/
@media (max-width: 32.1em) {
  .navHeader .dateFilterSettings {
    flex-direction: column;
  }
  .navHeader .dateFilterSettings .dropdownMenuContainer {
    border-right: none;
  }
  .navHeader .dateFilterSettings .shortcutDropdowns {
    position: static;
    align-items: center;
    margin-top: 1rem;
  }
  .navHeader .dateFilterSettings .fromToContainer {
    padding: 0.3rem 0;
  }
  .searchPage #resultsContainer {
    margin: 0 2rem;
  }
}
/*****
28.6em width and less
*****/
@media (max-width: 28.6em) {
  .navHeader .searchContainer {
    padding: 0 0 0 0.3rem;
  }
  .navHeader .brandLogo {
    display: none;
  }
  .navHeader #searchForm > input {
    font-size: 1.4rem;
  }
  .navHeader .rightContainer {
    min-width: unset;
    justify-content: flex-start;
    padding: 0 0.4rem;
  }
  .navHeader .rightContainer > div {
    padding: 0;
    flex: 0 0 auto;
  }
  /*****
    Prevent auto-zoom into the textarea
  */
  .navHeader .addPageUrls textarea {
    font-size: 16px;
  }
  .searchPage .mainResultLink {
    font-size: 1.2rem;
  }
  .searchPage .mainResultLink .material-icons {
    top: -1.2rem;
  }
  .searchPage .resultUrlText {
    font-size: 0.6rem;
  }
  .searchPage .safeBrowsing {
    font-size: 0.8rem;
  }
  .searchPage .safeBrowsing .material-icons {
    font-size: 1.2rem;
    padding-top: 0.1rem;
  }
  .searchPage .description {
    font-size: 0.75rem;
  }
  .searchPage .metaIcons a {
    font-size: 1.8rem;
  }
}
/*****
22.5em width and less
*****/
@media (max-width: 22.5em) {
  .navHeader .searchContainer {
    flex-basis: 14rem;
  }
  .navHeader .searchContainer #searchInput {
    flex-basis: 16rem;
    flex-grow: 0;
  }
}
